<template>
  <div id="app">
    <router-view />
    <el-backtop :bottom="50">
      <div
        style="
           {
            height: 100%;
            width: 100%;
            background-color: #E4A14B;
            box-shadow: 0 0 6px rgba(0, 0, 0, 0.12);
            text-align: center;
            line-height: 40px;
            color: white;
          }
        "
      >
        <i class="el-icon-top"></i>
      </div>
    </el-backtop>
  </div>
</template>

<script>

export default {
  name: "App",
  components: {

  },
  watch: {
    $route: function (to, from) {
      if (to.name.indexOf("_p") > 0 && !this.$pc) {
        console.log(to.name);
        if (to.name == "index_p") {
          let str = to.name.split("_p")[0];
          let str2 = "/" + str;
          this.$router.push(str2);
        }

        if (to.name == "commodity_p") {
          this.$router.push('/index');
        }

        if (to.name == "addus_p") {
          this.$router.push('/index');
        }

        if (to.name == "information_p") {
          this.$router.push('/index');
        }

        if (to.name == "story_p") {
          this.$router.push('/index');
        }

        if (to.name == "sp_1_p" || to.name == "sp_2_p" || to.name == "sp_3_p" || to.name == "sp_4_p" || to.name == "sp_5_p" || to.name == "sp_6_p" || to.name == "sp_7_p" || to.name == "sp_8_p" || to.name == "sp_9_p" || to.name == "sp_10_p" || to.name == "sp_11_p" || to.name == "sp_12_p") {
          this.$router.push('/index');
        }
        if (to.name == "wz_1_p" || to.name == "wz_2_p" || to.name == "wz_3_p" || to.name == "wz_4_p" || to.name == "wz_5_p" || to.name == "wz_6_p" || to.name == "wz_7_p" || to.name == "wz_8_p") {
          this.$router.push('/index');
        }

      } else if (to.name.indexOf("_p") < 0 && this.$pc) {
        // console.log(to.name);
        if (to.name == 'wz') {
          this.$router.push('/index_p')
        }else if (to.name == 'index') {
          this.$router.push('/')
        }else{
          this.$router.replace(to.name + "_p");
        }
      }
    },
  },
};
</script>

