import Vue from 'vue'
import App from './App.vue'

import router from './router'
import store from './store'
import Vant from 'vant';
import 'vant/lib/index.css';
import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';

// 网页进度
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

// 
import Meta from 'vue-meta';
// 使用vue-meta
Vue.use(Meta);

// 以下是vant组件
import { Toast } from 'vant';
import { Swipe, SwipeItem } from 'vant';
import { ShareSheet } from 'vant';

Vue.use(ShareSheet);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Toast);
// element的动画
// fade/zoom 等
import 'element-ui/lib/theme-chalk/base.css';
// collapse 展开折叠
import CollapseTransition from 'element-ui/lib/transitions/collapse-transition';
Vue.component(CollapseTransition.name, CollapseTransition)

// vue原型挂载 - 是否PC端
if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
  Vue.prototype.$pc = false
  // hostConfig.vconsole && new VConsole()
} else {
  Vue.prototype.$pc = true
  let winWidth = document.documentElement.offsetWidth ||
    document.body.offsetWidth
  winWidth = winWidth < 1366 ? 1366 : winWidth
  let oHtml = document.getElementsByTagName('html')[0]
  oHtml.style.fontSize = 100 * winWidth / 1920 + 'px'
  window.addEventListener('resize', function () {
    let winWidth = document.documentElement.offsetWidth || document.body.offsetWidth
    winWidth = winWidth < 1366 ? 1366 : winWidth
    let oHtml = document.getElementsByTagName('html')[0]
    oHtml.style.fontSize = 100 * winWidth / 1920 + 'px'
  })
}

NProgress.configure({
  easing: 'ease',  // 动画方式    
  speed: 500,  // 递增进度条的速度    
  showSpinner: false, // 是否显示加载ico    
  trickleSpeed: 200, // 自动递增间隔    
  minimum: 0.3 // 初始化时的最小百分比
})

Vue.prototype.$jq = $

Vue.use(ElementUI);
Vue.use(Vant);
Vue.config.productionTip = true

new Vue({
  router,
  store,
  render: h => h(App),
  // 预渲染
  // mounted() {
  //   document.dispatchEvent(new Event('render-event'))
  // }
}).$mount('#app')

router.beforeEach((to, from, next) => {
  // 只在页面进入加载一次，所以要用路由判断，让它重新进入index时再加载$(document).ready(function () {}
  //实现商品展示的js文件生效
  if (to.path == '/index_p' || to.path == '/commodity_p') {
    var SellerScroll = function (options) {
      this.SetOptions(options);
      this.lButton = this.options.lButton;
      this.rButton = this.options.rButton;
      this.oList = this.options.oList;
      this.showSum = this.options.showSum;

      this.iList = $("#" + this.options.oList + " > li");
      this.iListSum = this.iList.length;
      this.iListWidth = this.iList.outerWidth(true);
      this.moveWidth = this.iListWidth * this.showSum;

      this.dividers = Math.ceil(this.iListSum / this.showSum);//Ϊٿ 
      this.moveMaxOffset = (this.dividers - 1) * this.moveWidth;
      this.LeftScroll();
      this.RightScroll();
    };
    SellerScroll.prototype = {
      SetOptions: function (options) {
        this.options = {
          lButton: "left_scroll",
          rButton: "right_scroll",
          oList: "carousel_ul",
          showSum: 4//һιٸitems 
        };
        $.extend(this.options, options || {});
      },
      ReturnLeft: function () {
        return isNaN(parseInt($("#" + this.oList).css("left"))) ? 0 : parseInt($("#" + this.oList).css("left"));
      },
      LeftScroll: function () {
        if (this.dividers == 1) return;
        var _this = this, currentOffset;
        $("#" + this.lButton).click(function () {
          currentOffset = _this.ReturnLeft();
          if (currentOffset == 0) {
            for (var i = 1; i <= _this.showSum; i++) {
              $(_this.iList[_this.iListSum - i]).prependTo($("#" + _this.oList));
            }
            $("#" + _this.oList).css({ left: -_this.moveWidth });
            $("#" + _this.oList + ":not(:animated)").animate({ left: "+=" + _this.moveWidth }, {
              duration: "slow", complete: function () {
                for (var j = _this.showSum + 1; j <= _this.iListSum; j++) {
                  $(_this.iList[_this.iListSum - j]).prependTo($("#" + _this.oList));
                }
                $("#" + _this.oList).css({ left: -_this.moveWidth * (_this.dividers - 1) });
              }
            });
          } else {
            $("#" + _this.oList + ":not(:animated)").animate({ left: "+=" + _this.moveWidth }, "slow");
          }
        });
      },
      RightScroll: function () {
        if (this.dividers == 1) return;
        var _this = this, currentOffset;
        $("#" + this.rButton).click(function () {
          currentOffset = _this.ReturnLeft();
          if (Math.abs(currentOffset) >= _this.moveMaxOffset) {
            for (var i = 0; i < _this.showSum; i++) {
              $(_this.iList[i]).appendTo($("#" + _this.oList));
            }
            $("#" + _this.oList).css({ left: -_this.moveWidth * (_this.dividers - 2) });

            $("#" + _this.oList + ":not(:animated)").animate({ left: "-=" + _this.moveWidth }, {
              duration: "slow", complete: function () {
                for (var j = _this.showSum; j < _this.iListSum; j++) {
                  $(_this.iList[j]).appendTo($("#" + _this.oList));
                }
                $("#" + _this.oList).css({ left: 0 });
              }
            });
          } else {
            $("#" + _this.oList + ":not(:animated)").animate({ left: "-=" + _this.moveWidth }, "slow");
          }
        });
      }
    };
    $(document).ready(function () {
      var ff = new SellerScroll();
    });
  }

  //百度统计
  setTimeout(() => {

    var _hmt = _hmt || [];

    (function () {

      //每次执行前，先移除上次插入的代码

      document.getElementById('baidu_tj') && document.getElementById('baidu_tj').remove();   //判断有没有 baidu_tj  元素，有就删除

      var hm = document.createElement("script");

      hm.src = "https://hm.baidu.com/hm.js?0c0dcce365fc0c2c1868ec877aeafe1c";

      hm.id = "baidu_tj"

      var s = document.getElementsByTagName("script")[0];

      s.parentNode.insertBefore(hm, s);

    })();

  }, 0);

  next()

  // 每次切换页面时，调用进度条
  NProgress.start();
  // 若加载时间长且不定，担心进度条走完都没有加载完，可以调用　　NProgress.inc（）；//这会以随机数量递增，且永远达不到100%，也可以设指定增量    next();
  
  
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    // document.title = to.meta.title
  }
  next()

})

//当路由进入后：关闭进度条
router.afterEach(() => {

  // 路由跳转后到页面顶部
  window.scrollTo(0,0);
  // 在即将进入新的页面组件前，关闭掉进度条
  NProgress.done()
})

